.contact-content {
  display: flex;
  flex-direction: column;

  align-items: center;
  justify-content: center;

  text-align: center;
  width: 100%;
}

.contact-desc {
  font-size: 3vh;
}

.contact-methods {
  display: flex;
  flex-direction: column;

  align-items: center;
  justify-content: center;

  width: 100%;
  max-width: 100vh;
}

.contact-box {
  display: flex;

  align-items: center;
  justify-content: center;
  text-align: center;

  margin-left: 1vh;

  width: 100%;
  height: 7vh;

  border-radius: 1.5vh;
}

.contact-box p {
  font-size: 3vh;
  font-weight: bold;
  text-align: center;
}

.contact-box i {
  font-size: 4vh;
}

.contact-box a {
  color: inherit;
  text-decoration: underline;
}

.discord {
  color: #7289da;
}

.linkedin {
  color: #0a95db;
}

.email {
  color: #ff0000;
}
